export const mapFormat = filename => {
    if (filename) {
        let types = filename.split('.');
        let type = types.pop();
        let alias = '';
        switch (type) {
            case 'doc':
                alias = 'doc';
                break;
            case 'docx':
                alias = 'doc';
                break;
            case 'pdf':
                alias = 'pdf';
                break;
            default:
                alias = 'windows';
                break;
        }
        return alias;
    }
    return 'file'
}
