<template>
    <div class="list flex-container bg-grey">
        <div class="header shadow">
            <div class="container padding top">
                <div class="logo-wrap">
                    <div class="logo">
                        <img src="@/assets/logo.png" alt="aten logo">
                    </div>
                    <div class="header-title">
                        <span class="title">资源下载</span>
                    </div>
                </div>
                <div class="menus">
                    <ul>
                        <li>
                            <router-link to="/proj">项目</router-link>
                        </li>
                        <li>
                            <router-link to="/list">资源</router-link>
                        </li>
                    </ul>
                </div>
                <div class="search" :class="{ 'is-active': focus }">
                    <input type="text" @focus="focus = true" @blur="focus = false" v-model="keyword" placeholder="输入关键词搜索...">
                </div>
                <div class="intro">
                    <router-link to="/index/" v-if="userlimit > 100">管理资源</router-link>
                    <div v-else>
                        <span style="margin-right:20px">{{username}}</span>
                        <a href="javascript:" @click="logout">退出登录</a>
                    </div>
                </div>
            </div>
        </div>
        <div class="scroller-wrap"><div class="container scroller" ref="scroller" @scroll="onScroll">
            <div class="group" v-for="(group, groupIndex) in list" :key="groupIndex">
                <div class="item-title">{{ group.name }}</div>
                <div class="items">
                    <template v-for="(soft, softIndex) in group.lists">
                    <div class="item" :key="softIndex">
                        <div class="layout">
                            <div :class="'title ' + mapFormat(soft.file)">
                                <h3 :title="soft.name + ' ' + soft.version">{{ soft.name }} {{ soft.version }}</h3>
                                <span class="update el-icon-time">{{ soft.updated_at | formatDate }}</span>
                            </div>
                            <div class="desc">
                                <p>{{ soft.desc }}</p>
                            </div>
                            <div class="download"><p><span class="el-icon-download">下载次数:</span>{{ soft.download}}</p></div>
                            <div class="changelog">
                                <h4>更新日志</h4>
                                <p>{{ soft.log || '暂无' }}</p>
                            </div>
                            <div class="operation">
                                <el-link type="danger" icon="el-icon-collection-tag" v-if="soft.version !== ''" @click="$router.push({ name: 'historyversion', params: { id: soft.id } })">历史版本</el-link>
                                <el-link type="danger" icon="el-icon-collection-tag" v-else disabled title="暂无历史版本">历史版本</el-link>
                                <el-link type="danger" icon="el-icon-download" :href="filePath(soft)" :download="soft.file" v-if="soft.version !== ''">下载</el-link>
                                <el-link type="danger" v-else disabled title="暂无历史版本,不可下载" icon="el-icon-download">下载</el-link>
                            </div>
                        </div>
                    </div></template>
                </div>
            </div>
            <div class="empty-list" v-if="!list.length">
                <p>暂无符合条件的内容</p>
            </div>
            </div>
            <!-- <transition name="el-fade-in"><AliasNav v-show="aliasNavShow">
                <li
                v-for="{ name } in list"
                >
                    <a href="javascript:">
                        {{ name }}
                    </a>
                </li>
            </AliasNav></transition> -->
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex';
import { listGroup, getBaseUrl } from '@/libs/request';
import { getToken, removeCookie } from '@/libs/auth';
import { mapFormat } from '@/libs/tool';
import { formatDate } from '@/libs/moment';
import scrollBehavior from '@/mixins/scrollBehavior';
// import AliasNav from '@/components/aliasnav';

export default {
    data: () => ({
        resources: [],
        list: [],
        keyword: '',
        focus: false,
        // aliasNavShow: false,
    }),
    // components: { AliasNav },
    mixins: [scrollBehavior],
    methods: {
        listGroup(callback) {
            listGroup({
                success: res => {
                    this.resources = res.resources || [];
                    callback()
                },
                fail: err => {
                    this.$message.error(err)
                }
            })
        },
        filePath({ id, version }) {
            return getBaseUrl() + 'resource/download?id=' + id + '&version=' + version + '&token=' + getToken();
        },
        search() {
            let { keyword } = this;
            const searchField = (item, keys, keyword) => {
                let vals = keys.map(k => item[k].indexOf(keyword) !== -1);
                return vals.some(d => d);
            };
            this.list = keyword !== '' ? this.resources.map(item => {
                let filterList = item.lists.filter(soft => {
                    let _keyword = keyword.toLowerCase();
                    return searchField(soft, ['name', 'desc'], _keyword);
                });
                return filterList.length > 0 ? {
                    name: item.name,
                    lists: filterList,
                } : null
            }).filter(d => d) : this.resources;
            this.$nextTick(() => this.setScroll(this.$refs.scroller))
        },
        mapFormat,
        logout() {
            removeCookie();
            this.$router.push('/');
        },
        onScroll({ target }) {
            // this.aliasNavShow = target.scrollTop > 50;
        }
    },
    computed: {
        ...mapState(["userlimit", "username"])
    },
    filters: { formatDate },
    mounted() {
        this.listGroup(this.search)
    },
    watch: {
        keyword: 'search'
    }
}
</script>

<style scoped>
.menus ul {
    display: flex;
}

.menus ul li a {
    display: block;
    font-size: 18px;
    color: #666;
    padding: 0 10px;
}

.menus ul li .router-link-exact-active {
    color: #da251d;
}
</style>
